import orderList from "../../components/order-list/order-list.vue"
import uniPopup from '@/components/uni-popup/uni-popup.vue'
export default {
	components:{
		orderList,
		uniPopup
	},
	data() {
		return {
			type:'',
			message:"支付成功后，商家确认发货前可退，已发货后不可退",
			address:"陕西省西安市雁塔区高新四路和科技路交叉口处 高新九号广场10号楼",
			deliveryType:"快递运输",
			deliveryDate:"工作日与周末都可收货",
			tagHome:true,
			tagSchool:false,
			tagCompany:false,
			tagDefault:false,
			tagAnother:false,
			tagFirst:0,
			tagSecond:0,
			phoneNumber:'15029092647',
			tag:["快速配送","上门自提"],
			tagNext:["工作日与周末都可收货","只可工作日收货","只可周末收货"],
			
		}
	},
	
	onLoad() {

	},
	methods: {
		//弹框弹出和取消事件
		togglePopup(type, open) {
			this.type = type;
			this.$refs[open].open();
		},	
		cancel(type) {
			this.$refs[type].close()
		},
		//弹框状态
		change(e) {
			console.log(e.show)
		},
		//特产页面选择配送方式点击事件
		bindClick(text){
			console.log("点击成功"+text);
			this.deliveryType = text;
			this.tagFirst = !this.tagFirst;
			
		},
		//特产页面选择送货时间点击事件
		bindClick1(text,index){
			console.log("点击成功"+text);
			this.deliveryDate = text;
			this.tagSecond = index;
			
		},
		//点击提交订单跳转到支付订单页面
		goPayPage(){
			uni.navigateTo({
				url:"goodsPayPage"
			})
		},
		//点击地址部分跳转到地址页面
		goAddressPage(){
			console.log("跳转至地址页面")
			uni.navigateTo({
				url:"address"
			})
		}
			

	}
}
